import React from 'react'
import styled from 'styled-components'

const Window4 = styled.div`
    height: 100vh;
    width: 100%;
    background-image: url(/static/roman-bozhko-251412-unsplash.jpg);
    background-size: cover;
    background-attachment: fixed;
    h1 {
        position: relative;
        margin: 0 auto;
        width: 40%;
        text-align: center;
        top: 50vh;
        letter-spacing: 2px;
        font-family: "fjalla one";
        text-transform: uppercase;
        text-shadow: 2px 0px 5px black;
        font-size: 3vw;
        color: rgb(255, 255, 255);
    }
`;

const Map = styled.div`
    width: 100%;
    height: 100vh;
`;

const Address = styled.div`
    color: white;
    height: 25vh;
    margin: 0 auto;
    letter-spacing: 2px;
    font-family: "fjalla one";
    p {
        position: absolute;
        margin-top: 4vh;
        width: 20%;
        right: 10vw;
        font-size: 2vw;
    }
`;

const LocationPage = () => (
    <div>
        <Window4>
            <h1>Pop in for a bite and a brew.</h1>
        </Window4>

        <Map id="map">
            <script src="https://maps.googleapis.com/maps/api/js?callback=myMap"></script>
        </Map>

        <Address>
            <p>49 - 53 Courtney St<br />North Melbourne<br />3051</p>
        </Address>
    </div>
)

export default LocationPage


